import React, { useState, useEffect } from "react";
import axios from "axios";
import useForm from "../hooks/useForm";
import { Form } from "../styles";

function OneCard() {
  const [cards, setCard] = useState([]);
  const [form, handleInputs] = useForm();
  const isProduction = process.env.NODE_ENV === "production";
  const baseURL = isProduction
    ? "https://fideliuss.herokuapp.com/"
    : "http://localhost:3000";
  useEffect(() => {
    axios
      .get(`${baseURL}/cards/random?n=1`)
      .then(({ data }) => {
        setCard(prevState => {
          return [...prevState, ...data.cards];
        });
      })
      .catch(err => {
        console.log(err);
      });
  }, [baseURL]);

  const saveReading = () => {
    axios
      .post(`${baseURL}/savereading`, {
        ...form,
        cards
      })
      .then(({ data }) => {
        console.log("data", data);
        // props.history.push("/profile");
        return data.name;
      })
      .catch(err => {
        console.log(err);
      });
  };

  return (
    <div>
      <h1>Your Today Card</h1>

      {cards.map((card, i) => {
        return (
          <div key={i}>
            {!card ? (
              <p>Loading</p>
            ) : (
              <div key={i}>
                <p>{card.name}</p>
                <img src={card.img} alt="card" />
                <p>{card.meaning_up}</p>
                <p>{card.desc}</p>
              </div>
            )}
          </div>
        );
      })}
      <Form onSubmit={saveReading}>
        <input
          type="text"
          name="name"
          placeholder="name your reading"
          onChange={e => handleInputs(e)}
        />
        <button>Save Reading</button>
      </Form>
    </div>
  );
}

export default OneCard;
