import React, { createContext, Component } from "react";

export const MyContext = createContext();

class ContextProvider extends Component {
  state = {
    place: "Home",
    isAdmin: false,
    isLogged: false,
    user: null
  };

  changePlace = place => {
    this.setState({ place });
  };

  changeIsAdmin = isAdmin => {
    this.setState({ isAdmin });
  };

  changeIsLogged = isLogged => {
    this.setState({ isLogged });
  };
  login = user => this.setState({ user });
  logout = () => this.setState({ user: null });

  render() {
    const {
      changePlace,
      changeIsAdmin,
      changeIsLogged,
      state,
      login,
      logout
    } = this;
    return (
      <MyContext.Provider
        value={{
          state,
          changePlace,
          changeIsAdmin,
          changeIsLogged,
          login,
          logout
        }}
      >
        {this.props.children}
      </MyContext.Provider>
    );
  }
}

export default ContextProvider;
