import React, { useEffect, useState, useContext } from "react";
import AuthService from "../services/auth";
import { Button, Card } from "../styles";
import { Link } from "react-router-dom";
import axios from "axios";
import useForm from "../hooks/useForm";
import { MyContext } from "../services/Context";
function Profile(props) {
  const { state } = useContext(MyContext);
  console.log(state);
  const [form, handleInputs] = useForm();
  const [reading, setReading] = useState({});
  const [userData, setUser] = useState({});
  const [readings, setReadings] = useState([]);

  const isProduction = process.env.NODE_ENV === "production";
  const baseURL = isProduction
    ? "https://fideliuss.herokuapp.com/"
    : "http://localhost:3000";

  useEffect(() => {
    const authService = new AuthService();
    authService
      .profile()
      .then(res => {
        const userData = res.data.user;
        setUser(userData);

        const readings = res.data.user.readings;
        setReadings(readings);
      })
      .catch(err => {
        if (err.res === 401) {
          console.log(err.res);
        }
      });
  }, [baseURL]);

  const updateReading = id => {
    axios
      .patch(`${baseURL}/reading/${id}`, form)
      .then(({ data }) => {
        setReading(data.reading);
      })
      .catch(err => {
        console.log(err);
      });
  };
  if (!state.user) return <p>Loading...</p>;
  return (
    <div className="profile">
      <h2>Hello {state.user.name} </h2>
      <h3>Your readings </h3>
      {readings.map(reading => {
        return (
          <div key={reading._id}>
            <Card>
              <Link to={`/savedreading/${reading._id}`}>
                <h2>{reading.name}</h2>
              </Link>
              <br />
              <input
                type="text"
                name="name"
                placeholder="new name"
                onChange={e => handleInputs(e)}
              />
              <Button onClick={() => updateReading(reading._id)}>
                Change name
              </Button>
            </Card>
            <br />
          </div>
        );
      })}
    </div>
  );
}

export default Profile;
